import { useState } from 'react';
import { Link } from 'react-router-dom'
import { contactInfo } from '../../Raw/info';
import { generalNavLinks } from '../../Raw/menuLinks';
import { showMenuAndMoveMainAnimation } from '../../Tools/animations';
import { SimpleImage } from '../img/global'
// import { GeneralSideBar } from './sidebar';
// import { generalNavLinks } from '../../Raw/menuLinks';


export const GeneralNav = () => {

    function showNext(e) {
        // console.log(e.currentTarget.nextSibling);
        e.currentTarget.nextSibling.classList.toggle("getFullHeight")
    }

    return <>
        <section className='gnAuthAndContact'>
            <section className='gaac-1'>
                <span><i className='mdi mdi-phone'></i>{contactInfo.tel} </span>
                <span><i className='mdi mdi-gmail'></i>{contactInfo.mail}</span>
            </section>
            <section className='gaac-2'>
                <span><i className='mdi mdi-facebook'></i></span>
                <span><i className='mdi mdi-whatsapp'></i></span>
                <span><i className='mdi mdi-linkedin'></i></span>
            </section>
        </section>
        <nav className='generalNav'>
            <section className='gnLogo'>
                {/* <SimpleImage props={{ src: 'logob.png', alt: 'Logo non Img', classname: '' }} /> */}
                <b>DidaFir Coaching</b>
            </section>

            <section className='gnNavLinks'>
                {generalNavLinks.map((link, index) => <div
                    key={'nav link' + index}>
                    {
                        <Link to={link.link} >{link.label}</Link>
                    }
                </div>)}
                <Link to="" className='signBtn'>S'inscrire</Link>
            </section>

            <section className='gnPcNavLinks'>
                <span onClick={(event) => showNext(event)}>
                    <i className='mdi mdi-menu'></i>
                </span>
                <section>
                    {
                        generalNavLinks.map((link, index) => <Link to={link.link}
                            key={"pc link nb" + index}
                        >{link.label}</Link>)
                    }

                </section>
            </section>


            <section className='gnHambuger' onClick={(event) => showMenuAndMoveMainAnimation()}>
                <span>Menu</span>
                <i className='mdi mdi-menu'></i>
            </section>

        </nav >
        {/* <GeneralSideBar props={{ menuObject: dispatchNavLinks(), subMenuKey: 'subLinks', authed }} /> */}
    </>
}



export const AuthLinks = ({ props }) => {
    return <>
        <Link to='/Connexion'>Connexion</Link>
        <Link to='/Inscription'>Inscription</Link>
    </>
}