import { Route, Routes, useLocation } from "react-router-dom";
import './Assets/css/default.css';
import './Assets/css/app.css';
import './Assets/css/modal.css';
import './Assets/css/form.css';
import "./Assets/css/slick/slick.css";
import "./Assets/css/slick/slick-theme.css";
import './Assets/css/materialdesignicons.min.css'
import './Assets/css/otherStyle.css';
import './Assets/js/scroll.js';
import { GeneralNav } from "./GlobalComponents/navigation/navbar";
import { useEffect } from "react";
import Accueil from "./Routes/Accueil";
import { Footer } from "./GlobalComponents/footer/footer";
import About from "./Routes/About";
// import Faq from "./Routes/Faq";
import Contact from "./Routes/Contact";
import Inscription from "./Routes/Inscription";
import Maintenance from "./Routes/Maintenance";

function App() {
  const location = useLocation();
  const routesTables = [
    {
      link: '/', components: <Accueil />, requireAuth: 'both'
    },
    {
      link: '/A-propos', components: <About />, requireAuth: 'both'
    },
    {
      link: '/Contact', components: <Contact />, requireAuth: 'both'
    },
    {
      link: '/Inscription', components: <Inscription />, requireAuth: 'both'
    },
    {
      link: '/*', components: <Maintenance />
    },
  ]

  useEffect(() => {
    const navIsOpen = document.querySelector('.getFullHeight')

    if (navIsOpen) {
      navIsOpen.classList.toggle('getFullHeight')
    }

  }, [location]);


  return (
    <div className="App">
      <main>
        <header>
          <GeneralNav />
        </header>

        <Routes>

          {
            routesTables.map((route, index) => <Route key={'nim num route' + index}
              path={route.link}
              element={route.components} />)
          }

        </Routes>

      </main>
      <Footer />
    </div>
  );
}

export default App;
