import React from 'react'
import { Link } from 'react-router-dom'

function Maintenance() {
    return (
        <div className='maintenance'>
            <img src={require('../Assets/images/illustrations/Construction.svg').default} alt='Page en maintenance image' />
            <h1>Désolé,cette page est en cours de maintenance,veullez revenir dans quelques jours,merci.....</h1>
            <Link to="/">Revenir a l'accueil</Link>
        </div>
    )
}

export default Maintenance
