import { SimpleImage } from "./img/global";
import { getError } from "../Tools/formValidator";
import { dispatchInputOrSelect } from "../Tools/formValidator";

export const OtherPageBanner = ({ props }) => {
    const { page } = props;
    const opbData = {
        contact: { title: 'Contact', subTitle: 'Lorem ipsum dolo Contact', img: 'contact.jpg' },
        apropos: { title: 'A propos', subTitle: 'Lorem ipsum dolo Apropos', img: 'apropos.jpg' },
        inscription: { title: 'Inscription', subTitle: 'Lorem ipsum dolo Apropos', img: 'apropos.jpg' },

    }

    return <div className="otherPageBanner">
        <SimpleImage props={{ src: 'visuels/' + opbData[page].img, alt: 'Contact lol', className: "" }} />
        <section>
            <h1>{opbData[page].title}</h1>
            <p>{opbData[page].subTitle}</p>
        </section>
    </div>
}

export const OtherPageTitle = ({ props }) => {
    const { page } = props;
    const opbData = {
        contact: { title: 'Disponible 24h/24', icon: 'mdi-contacts' },
        apropos: { title: "Ce qu'il faut savoir", icon: 'mdi-account-question-outline' },
        inscription: { title: "Inscrivez vous en quelque clics", icon: 'mdi-account-details-outline' },

    }

    return <section className="otherPageTitle">
        <i className={"mdi " + opbData[page].icon}></i>
        <h2>{opbData[page].title}</h2>
    </section>
}


export const SubscriptionForm = ({ props }) => {
    const { formFields,
        handleSubmit, handleChange,
        formValues, setFormValues,
        errors, setErrors,
        formBtn, subscriptionPrice } = props;
    return <>
        <div className="checkOutTitle">
            <b>Paiement 100% sécurisé</b>
            <SimpleImage props={{ src: "paiement.png", alt: "Piament pic", className: "" }} />
        </div>

        <form className="subscriptionForm" onSubmit={(event) => handleSubmit(event)}>
            {
                formFields.map((field, index) => <div key={'auth field' + index}
                    className={field.size ? "formSegment " + field.size : "formSegment fsFull"} >
                    <label>{field.label}</label>
                    <section >
                        {
                            dispatchInputOrSelect(formValues, setFormValues,
                                field, handleChange,
                                errors, setErrors)
                        }
                    </section>
                    {getError(field.name, errors)}
                </div>)
            }

            <div className="formTotalPrice">
                <p>Total a payer :</p>
                <b>{subscriptionPrice} €</b>
            </div>
            <div className="formBtn">
                {formBtn}
            </div>
        </form>
    </>
}