import { useState } from "react";
import { Fade, Reveal, } from "react-reveal";
import { Link } from "react-router-dom"
import { SimpleImage } from '../GlobalComponents/img/global';
import { contactInfo } from "../Raw/info";
import ReactPlayer from 'react-player'


const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
};

export const Banner = () => {
    return <div className='hBanner'>
        <section className='hb-text'>
            <Fade>
                <h1>{contactInfo.nom} {contactInfo.prenom} </h1>
            </Fade>
            <Fade bottom>
                <p>« Nous sommes dangereux quand nous ne sommes pas conscients que nous
                    sommes responsables de nos actes, de nos pensées et de nos sentiments . »
                    <br />
                    <b>- Marshall Rosenberg</b>
                </p>
            </Fade>
            <div>
                <Reveal cascade bottom>
                    <Link to="/Contact">Nous contactez</Link>
                    <Link to="/Inscription" className="signBtn">S'inscrire</Link>
                    {/* <Link to="">En savoir plus</Link> */}
                </Reveal>
            </div>

        </section>

    </div>
}

