import '../Assets/css/inscription.css'
import { useEffect, useState } from "react";
import { OtherPageBanner, OtherPageTitle } from "../GlobalComponents/OtherOnWebsite";
import { allFields } from "../Raw/fields";
import { dispatchInputOrSelect } from "../Tools/formValidator";
import { handleUserFieldInfoChange } from "../Tools/formFunction";
import { dispatchClassOnDevice } from '../Assets/js/domManipulation';

const Inscription = () => {
    const [formFields, setFormFields] = useState([allFields.individu_ou_groupe])
    const [formValues, setFormValues] = useState({
        individu_groupe: '',
        nom_groupe: '',
        nom: '', prenom: '', adresse: ''
    })
    const [errors, setErrors] = useState([])


    useEffect(() => {
        const newFields = [allFields.individu_ou_groupe];
        if (['Association(Local)-Entreprise de moins de 10 salarié',
            'Entreprises- Organisations et Institutions  de plus de 10 Salariés']
            .includes(formValues.individu_groupe)) {
            newFields.push(allFields.nom_groupe)
        } else if (formValues.individu_groupe == 'Particulier') {
            newFields.push(allFields.nom, allFields.prenom)
        }

        if (formValues.individu_groupe != "") {
            newFields.push(allFields.mail, allFields.telephone,
                allFields.pays, allFields.adresse, allFields.codepostal,
                allFields.description)
        }
        setFormFields(newFields)
    }, [formValues.individu_groupe])


    return <div className="inscription">
        <OtherPageBanner props={{ page: 'inscription' }} />
        <OtherPageTitle props={{ page: 'inscription' }} />
        <section className="inscription-form">
            <form>
                {
                    formFields.map((field, index) => <div
                        className={"formSegment " +
                            (field.size === 'fsSemi' ? dispatchClassOnDevice('fsFull', 'fsSemi') : 'fsFull')}
                        key={'inscription form field nb' + index}>
                        <label>{field.label} </label>
                        <section >
                            {
                                dispatchInputOrSelect(formValues, setFormValues,
                                    field, handleUserFieldInfoChange, errors, setErrors)
                            }
                        </section>
                    </div>)
                }
                <div className='formBtn'>
                    <button>Proceder a l'inscription</button>
                </div>
            </form>
        </section>
    </div>
}



export default Inscription;