import '../Assets/css/apropos.css'
import { SimpleImage } from "../GlobalComponents/img/global";
import { OtherPageBanner, OtherPageTitle } from "../GlobalComponents/OtherOnWebsite";

const About = () => {

    const aboutBiographie = [
        { title: "", text: "" }
    ]
    return <div className="about">
        <OtherPageBanner props={{ page: 'apropos' }} />
        <div>
            <OtherPageTitle props={{ page: 'apropos' }} />

            <section className="about-me-Info">
                <div className='ami-img'>
                    <SimpleImage props={{ src: 'coachPics/1.1.png', alt: 'Coach Picture', className: "" }} />
                </div>

                <div className='ami-text'>
                    <ul>
                        <li>
                            <h3>Origine et motivations</h3>
                            <p>
                                Béninois d’origine, je travaille depuis plus de trente ans dans des écoles de mon pays.
                                Ayant grandi durant ma vie scolaire dans un système scolaire qui use de violences verbales et physiques, ayant été une victime de ce système, j’ai appris tout au long de ma vie à fuguer dans la nature. En ce lieu, je me suis toujours posé la question de savoir pourquoi les éducateurs que je rencontrais, utilisaient toutes formes de violences comme stratégie pour gérer les enfants.
                                Pourquoi existent-ils des éducateurs qui boivent, violent, tuent, abandonnent les enfants ?
                            </p>
                        </li>

                        <li>
                            <h3>Découverte de la CNV</h3>
                            <p>
                                j’ai découvert à travers ma rencontre avec la CNV depuis 2016,
                                la beauté de « mon enfant intérieur» qui m’a permis très tôt de choisir de restaurer ma personne
                                mais aussi de restaurer l’humanité. Ma rencontre avec la CNV m’a permis de comprendre
                                que j’ai su transformer ma vulnérabilité en une force de vie, en une croyance à la bienveillance
                                de l’être humain. Ma rencontre avec la CNV en 2016 marque un tournant important dans ma vie,
                                tant dans mon aptitude à prendre soin de moi-même et des autres que dans ma façon d’accompagner
                                l’équipe pédagogique, les parents, ma famille, les enfants. J’ai été et reste profondément touché
                                par la capacité d’accueil inconditionnel et par la
                                bienveillance que permet la CNV ainsi que par la puissance transformatrice de ce processus.
                            </p>
                        </li>

                        <li>
                            <h3>Origine et motivations</h3>
                            <p>
                                A partir de maintenant, je choisis en tant qu’acteur et formateur en Communication NonViolente,
                                de travailler pour un changement de paradigme dans les écoles du Bénin. A partir de maintenant,
                                je choisis de vivre la paix pour rayonner la paix au niveau du système éducatif
                                (Elève-parent-enseignant). A partir de maintenant, je vis ce rêve dans les écoles,
                                dans le réseau ACNV-Bénin(Association pour la Communication NonViolente Bénin).
                            </p>
                        </li>

                    </ul>
                </div>


            </section>
        </div>
    </div>
}

export default About;