export function dispatchClassOnDevice(classOnMobile, classOnPc) {
    var condMobile = window.matchMedia("(max-width: 512px)")
    if (condMobile.matches) {
        return classOnMobile
    } else {
        return classOnPc
    }
}

export function closeIfClickAnywhere(modalSetter) {
    document.addEventListener('click', (e) => {
        const modal = document.querySelector('.modal')
        if (modal && modal === e.target) {
            modalSetter(false)
        }
    })
}